.content {
    display: grid;
    grid-template-areas:
    "a p";
    grid-auto-columns: 4fr 8fr;
}
.avatar {
    grid-area: a;
    width: 300px;
}
.profile {
    grid-area: p;
    float: right;
}
.avatar img {
    margin-left: 15px;
    width: 200px;
}
.mainPhoto {
    margin-left: 15px;
    opacity: 50%;
}
.mainPhoto:hover {
    opacity: 100%;
}

.name {
    margin-top: 0;
    font-size: 24px;
    line-height: 1.5;
    margin-bottom: 0;
}
.status {
    margin-bottom: 20px;
    color: #666666;
}
.contact {
    padding-left: 10px;
}
.button {
    margin-left: 15px;
}
.button label{
    /*opacity: 50%;*/
    width: 200px;
}
.button:hover label{
    /*opacity: 100%;*/
    width: 200px;
}
